@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

.nav {
  display: flex;
  align-items: center; }
  .nav__link {
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    line-height: 1.375rem;
    font-weight: bold;
    color: #fff; }
    .nav__link + .nav__link {
      margin-right: 8.75rem; }

.header {
  background-color: #fff;
  padding: 0.75rem 8.75rem; }
  .header--theme-dark {
    background-color: #707070; }
  .header__main {
    display: flex;
    align-items: center;
    justify-content: space-between; }


html {
  box-sizing: border-box;
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Ubutntu', 'Robot', sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%; }

button {
  border: none; }

button:hover {
  cursor: pointer; }

